@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Codystar&display=swap);
body {
  margin: 0;
  font-family:'Courier New', Courier, monospace;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Courier New", Courier, monospace;
}
body {
  background-color: #fff;
  width: 100%;
}

#connect {
  background-color: #ffffff;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.connect :link {
  text-decoration: none;
}

.connect-desc div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 30vh;
  padding: 10px 20px;
}

.connect-desc div:hover {
  background-color: #7fcbcd;
  transition: 0.3s ease;
}

.connect-niser,
.connect-MLab {
  padding: 20px 20px !important;
}

.club-details-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: auto;
  z-index: 2;
  position: absolute;
  bottom: 20px;
  width: 100%;
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #80ccce;
}

.club-details-container > a {
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: 4em;
  padding-top: 20px;
  padding-bottom: 10px;
  z-index: 10;
}
body {
  scroll-behavior: smooth;
}

a:-webkit-any-link {
  text-decoration: none !important;
}

a:hover,
a:link,
a:active,
a:any-link,
a:visited {
  text-decoration: none !important;
}

.mail,
.whatsapp,
.yt,
.fb,
.insta,
.github,
.rg {
  color: black;
  transition: 0.5s ease;
}

.mail:hover {
  opacity: 1;
  color: rgb(0, 114, 202);
}

.whatsapp:hover {
  opacity: 1;
  color: rgb(85, 240, 112);
}

.yt:hover {
  opacity: 1;
  color: #f70000;
}

.fb:hover {
  opacity: 1;
  color: #3b5998;
}

.insta:hover {
  opacity: 1;
  color: #fb3958;
}

.github:hover {
  opacity: 1;
  color: rgb(23, 21, 21);
}

.rg:hover {
  opacity: 1;
  color: rgb(30, 151, 95);
}
#connect-logo {
  -webkit-align-content: center;
          align-content: center;
  width: 12rem;
  opacity: 0.75;
  transition: 0.3s ease;
}

#connect-logo:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

#niser-link {
  color: black !important;
}

#niser-link:hover {
  color: rgba(255, 255, 255, 0.6) !important;
  mix-blend-mode: difference;
}

.connect_new {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
  width: 100%;
  background-image: url(/static/media/AD21-ZoomWallpaper-Lighblue.3823642c.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 50vh;
  position: relative;
  bottom: 0;
}

.connect_new_blur {
  background-color: rgba(46, 42, 45, 0.877);
  color: rgb(255, 255, 255);
  width: 100%;
  height: 50%;
  position: absolute;
  top: 30%;
  z-index: 1;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 1vh;
}

.connect_new_content {
  width: 100%;
  position: absolute;
  z-index: 1;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding-top: 10px;
}
.connect_new_content_text {
  text-align: left;
  z-index: 1;
}
.connect_new_content_text a {
  color: whitesmoke;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
.connect_new_content_text a:hover {
  color: rgb(132, 132, 132);
}

.connect_new_content_logo {
  z-index: 1;
  max-height: 30%;
}

@media screen and (max-height: 880px) {
  .club-details-container > a {
    font-size: 2.8em;
  }
  .club-details-container {
    bottom: 20px;
  }
  .connect_new {
    min-height: 100vh;
  }
  .connect_new_blur {
    height: 80%;
    top: 20%;
  }
  .connect_new_content {
    width: 100%;
  }
  .connect_new_content_text {
    padding: 8px 0 0 15px;
  }
}
@media screen and (max-height: 640px) {
  .connect_new_blur {
    top: 10%;
    bottom: 0;
  }
}

@media screen and (max-width: 650px) {
  .connect_new_blur {
    height: 90%;
    top: 20%;
    bottom: 0;
  }
  .connect_new_content {
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
}

